  .file label{
        display: inline-block;
    width: 100px;
    height:45px;
    background-color: #4a4a4a;
    color:#fff;
    cursor: pointer;
    line-height: 45px;
    border-radius: 5px;
    text-align: center
  }
  .file input[type="file"]{
         position: absolute;
    width:1px;
    height:1px;
    margin:-1px;
    clip:rect(0,0,0,0);
    overflow: hidden;
    padding:0;
  }

  .filebox input[type="file"] {
    position: absolute;
    width: 0;
    height: 0;
    padding: 0;
    overflow: hidden;
    border: 0;
  }
  
  .filebox label {
    display: inline-block;
    padding: 10px 20px;
    color: #999;
    vertical-align: middle;
    background-color: #fdfdfd;
    cursor: pointer;
    border: 1px solid #ebebeb;
    border-radius: 5px;
  }
  
  /* named upload */
  .filebox .upload-name {
    display: inline-block;
    height: 35px;
    font-size:18px; 
    padding: 0 10px;
    vertical-align: middle;
    background-color: #f5f5f5;
