      -webkit-transform: translateY(0);
              transform: translateY(0);
    }
    50% {
      -webkit-transform: translateY(20px);
              transform: translateY(20px);
    }
}
@keyframes pendulum {
   0%, 100% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
    }
    50% {
      -webkit-transform: translateY(20px);
              transform: translateY(20px);
    }
}

@-webkit-keyframes fadeOut {
   100% {-webkit-transform: scale(0.4);transform: scale(0.4); opacity: 0;}
   }

@keyframes fadeOut {
   100% {-webkit-transform: scale(0.4);transform: scale(0.4); opacity: 0;}
